import { NumberInput } from "@mantine/core";
import { Dispatch, FC, ReactNode, SetStateAction, useCallback } from "react";
import { Shape } from "../../types";
import { DrawingParams } from "../Layout";
import * as S from "./styles";

type NavItem = {
  image: string;
  meta?: ReactNode;
};

type Props = {
  onChange: Dispatch<SetStateAction<DrawingParams>>;
  stroke: DrawingParams["strokeWidth"];
};
export const Navbar: FC<Props> = ({ onChange, stroke }) => {
  const setStroke = useCallback(
    (val: number) =>
      onChange((prev) => ({
        ...prev,
        strokeWidth: val,
      })),
    []
  );

  const setShape = useCallback(
    (val: Shape) =>
      onChange((prev) => ({
        ...prev,
        shape: val,
      })),
    []
  );
  const navbarConfig: Record<Shape, NavItem> = {
    Brush: {
      image: "",
      meta: (
        <NumberInput
          style={{ width: "60px" }}
          value={stroke}
          min={1}
          max={99}
          size="xs"
          onChange={(value: number) => {
            setStroke(value);
          }}
        />
      ),
    },
    Image: {
      image: "",
      meta: (
        <S.ImageInput
          onChange={(e) => {
            console.log(e.target.files);
          }}
          type="file"
          accept="image/png, image/gif, image/jpeg"
        />
      ),
    },
  };

  return (
    <S.List>
      {Object.entries(navbarConfig).map(([key, { meta }]) => (
        <S.Item key={key} onClick={() => setShape(key as Shape)}>
          {key}
          {meta}
        </S.Item>
      ))}
    </S.List>
  );
};
